nav {
    font-size: 32px;
    font-family: 'WaltographRegular'; 
    font-weight: bolder; 
    font-style: normal; 
    padding: 20px;
    background-color: lightpink;
    color: white;
  }

  .title {
    float: left;
  }
  
  .header {
    float: left;
    margin-left: 30%;
  }
  .score {
    float: right;
  }

  
.jumbotron {
    background-image: url(/clicky-game/static/media/background.bb315ce3.png);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    min-height: 100vh;
    color: hotpink;
    margin-bottom: 0;
    background-color: lightpink;
  }

  h1 {
    padding-top: 390px;
    letter-spacing: 0.1ch;
    font-family: 'WaltographRegular'; 
    font-weight: bolder; 
    font-style: normal; 
    font-size: 50pt;
  }
  
  h3 {
    font-family: 'WaltographRegular'; 
    font-size: 30pt;
  }
.card {
    display: inline-block;
    margin: 10px;
}

.cardImg {
    width: 150px;
    height: 150px;
    border: 5px solid rgb(111, 206, 238);
}

.cardImg:hover {
    cursor: pointer;
}

.container {
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding-top: 25px;
    padding-bottom: 25px;
    justify-content: space-around;
    align-content: flex-start;
}

#root {
    background-color: lightpink;
}
body {
    padding: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
